@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    padding: 0;
    margin: 0;
  }
  .grid-container {
    display: grid;
    width: 100vw;
    height: 100vh;
    grid-template-rows: 10vh 35vh 35vh 20vh;
    grid-template-columns: 5vw auto 20vw 20vw 10vw;
    grid-gap: 0px;
  }
  .item {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
  }
  .title-header {
      color: rgb(0, 0, 0);
      font-size: 48px;
      font-family: 'Open Sans';
      font-weight: 600;
      text-decoration: underline;
      padding-top: 100px;
      padding-right: 100px;
  }
  .subtitle-header {
      color: rgb(0, 0, 0);
      font-size: 32px;
      font-family: 'Open Sans';
      font-weight: 400;
      padding-right: 113px;
  }
  .white-bg{
    background-color: white;
  }
  .navy-bg{
    background-color: #14213D;
  }
  .circle-item {
    display: flex;
    justify-content: space-around;
    align-items: center;
    
    background: rgb(255,255,255);
    background: linear-gradient(90deg, rgba(255,255,255,1) 50%, rgba(20,33,61,1) 50%);
    grid-column-start: 3;
    grid-column-end: span 2;
    grid-row-start: 2;
    grid-row-end: span 2;
  }
  #record-container{
    display: flex;
    justify-content: space-around;
    align-items: center;
    
    background: #B3679B;
    height: 70vh;
    width: 70vh;
    -webkit-filter: drop-shadow(1px 1px 0px #B3679B) drop-shadow(-1px 1px 0px #B3679B) drop-shadow(1px -1px 0px #B3679B) drop-shadow(-1px -1px 0px #B3679B);
            filter: drop-shadow(1px 1px 0px #B3679B) drop-shadow(-1px 1px 0px #B3679B) drop-shadow(1px -1px 0px #B3679B) drop-shadow(-1px -1px 0px #B3679B);
    border-radius: 50%;
  }
  #record {
    width: 90%;
    height: 90%;
    background-color: white;
    border-radius: 50%;
    transform: rotate(-5deg);
  }
  .left-border{
    border-left: 20px solid #B3679B;
  }
  .right-border{
    border-right: 20px solid #B3679B;
  }
  .main-button {

    color: black;

    font-family: 'Open Sans';
    font-weight: 800;
    font-size: 2em;
    text-decoration: none;
    margin-top: 150px;
    margin-right: 110px;
    padding: 0.7em;
    border: 2px solid black;
    
    border-radius: 1em;
    
}
.main-button:hover{
    transition: background-color 0.1s;
    background-color: rgb(30 215 96);
    color: black;
}
.data-page-main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  text-align: center;
  background-color: #14213D;
  color: white;
  padding: 0px;
  margin: 0px;
  height: 100vh;
}

.data-body{
  display: grid;
  grid-template-columns: auto auto;
  margin: 0px;
  padding: 0px;
  border: 2px solid black;
}
.sorting-button-container{
  background-color: white;
  display: flex;
  flex-direction: column;
  height: 75vh;
  width: 10vw;
  margin: 0px;
  padding: 20px;
  border-right: 1px black solid;
}
.sorting-button-container > h3 {
  color: black;
  font-size: 18px;
  padding-bottom: 10px;
}
.sorting-button-container > h4 {
  color: black;
  font-size: 16px;
  padding-bottom: 5px;
}
.filter-buttons {
  margin: 20px 0px 20px 0px;
}
.card-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  align-content: flex-start;
  background-color: #E5E5E5;
  width: 75vw;
  height: 75vh;
  align-self: center;
  padding: 20px;
  margin: 0px;
  overflow: auto;
  border-left: 1px black solid;
}
.error-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;

  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  color: white;
}
.header {
  width: 100%;
  text-align: center;
  font-size: 6vmin;
  padding: 20vh 0 5vh 0;
  color: white;
}
.above-playlist-container {
    display: inline-block;
    text-align: center;
    width: 100%;
    height: 7vh;
    padding: 2vh 0vh 2vh 0vh;
}

.playlist-number{
    margin-left: auto;
}
.footer {
  width: 100%;
  text-align: center;
  padding: 2vh 0 1vh 0;
  color: white;
}
.AnalysisPage {
    display: flex;
    
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    min-height: 100vh;
    width: 100%;
    
    background-color: #14213D;;
}
.AnalysisPageBody{
    display: grid;
    grid-template-columns: auto auto;
    margin: 0px;
    padding: 0px;
    max-height: 80vh;
    border: 2px solid black;
    
    overflow: visible;
    box-shadow: 0 3px 6px -4px rgba(0, 0, 0, 0.36), 0 6px 16px 0 rgba(0, 0, 0, 0.24),
    0 9px 28px 8px rgba(0, 0, 0, 0.15);
}
.tabContainer {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    height: calc(80vh + 40px);
    width: calc(10vw + 40px);
    background-color: #E5E5E5;
}
.tabContainer > Button {
    width: 80%;
    font-size: 32px;
    padding: 10px;
    margin: 20px;
    font-family: 'Open Sans';
    font-weight: 550;
    line-height: 120%;
}
.dataContainer {
    display: inline-block;
    width: 70vw;
    height: 80vh;
}
.songPlotContainer {
    background-color: white;
    height: 80vh; 
    width: 100% - 40px;
    padding: 20px;
}
.popularArtistChartContainer {
    background-color: white;
    height: 80vh; 
    width: 100% - 40px;
    padding: 20px;
}
.songChartContainer {
    background-color: white;
    height: 80vh; 
    width: 100% - 40px;
    padding: 20px;
}
.dataCardHeader {
    color: black;
}
.loading-text {
    color: white;
    font-size: 32px;
    font-weight: 500;
    line-height: 120%;
}

